.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--container-color);
}

.intro{
    max-width: 540px;
    text-align: center;
    z-index: 1;
}

.home__img{
    margin-bottom: 1.5rem;
}

.home__name{
    color: var( --blanco-color  ) ;
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__education{
    color: var( --texto-color )
}

.home__socials{
    display: flex;
    justify-content: center;
    column-gap: 1.75rem;
    margin: 1.5rem 0;
}

.home__social-link{
    color:var( --texto-color ) ;
    font-size: 1.3rem;
    transition: .3s;
}

.home__social-link:hover{
    color: var( --title-color );
}

.scroll_down{
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.home__scroll-name{
    font-size: var(--small-font-size);
    color:var(--blanco-color)
}

.mouse{
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
    background-color:var(--blanco-color)
}

.mouse:hover{
    background-color: var(--text-color);
    border:var(--title-color) #454360;
}

@keyframes ani-mouse {
    0%{
        top:29%
    }
    15%{
        top:50%
    }
    50%{
        top:50%
    }
    100%{
        top:29%
    }

}

.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

.shapes{
    position: absolute;
    left: 0;
    top: 0;
}

.btn{
    background-color: var( --title-color );
    margin: auto;
}

@media screen and (max-width:1024px){
    .home__social-link{
        font-size: 1.125rem;
    }
}
